	const tools = require('@/common/tools.js');
	const server = require('@/common/server.js');
	



	var sourceType = [
		['camera'],
		['album'],
		['camera', 'album']
	]
	var sizeType = [
		['compressed'],
		['original'],
		['compressed', 'original']
	]
	export default {
		data() {
			const date = this.getDate();
			return {
				date,
				index: 0,
				typeList:[],
				typeNameList:[],
				typeId:0,
				sourceType: ['拍照', '相册', '拍照或相册'],
				sizeTypeIndex: 2,
				countIndex: 8,
				imageList: [],
                count: [1, 2, 3, 4, 5, 6, 7, 8, 9],
				server:server.domain,
				hospital:"",
				disease:"",
				diseaseImgList:[],
				id:""
			}
		},
		onLoad(options) {
			if(options.id){
				this.id = options.id;
				this.getDetail()
			}
			this.getTypeList();
		},
		methods: {
			getDetail(){
				let that = this;
				let params = { 
					id:this.id
				};
				tools.request("/api/my/treatmentRecordDetails.json", params,1,true).then(function(data) {
					
					that.item = data;
					console.log(that.item);
					that.date = that.item.treatmentTime;
					that.hospital = that.item.hospital;
					that.disease = that.item.disease;
					that.diseaseImage = that.item.diseaseImgList
				})
			},
			submit(){
				if(tools.isEmpty(this.hospital,"请输入就诊医院")){return;}
				if(tools.isEmpty(this.disease,"请输入所患病情")){return;}
				let that = this;
				let params = { 
					hospital:this.hospital,
					treatmentTimeString: this.date,
					disease:this.disease,
					diseaseImage: this.diseaseImgList.toString().replace(new RegExp(",","g"),";"),
					typeIllnessId:this.typeId,
				};
				tools.request("/api/my/addTreatmentRecord.json", params,1,true).then(function(data) {
					tools.toastJumpTab("添加病例成功", "/pages/index/index");
				})
			},
			async checkPermission(code) {
			    let type = code ? code - 1 : this.sourceTypeIndex;
			    let status = permision.isIOS ? await permision.requestIOS(sourceType[type][0]) :
			        await permision.requestAndroid(type === 0 ? 'android.permission.CAMERA' :
			            'android.permission.READ_EXTERNAL_STORAGE');
			
			    if (status === null || status === 1) {
			        status = 1;
			    } else {
			        uni.showModal({
			            content: "没有开启权限",
			            confirmText: "设置",
			            success: function(res) {
			                if (res.confirm) {
			                    permision.gotoAppSetting();
			                }
			            }
			        })
			    }
			
			    return status;
			},
			isFullImg: function() {
			    return new Promise((res) => {
			        uni.showModal({
			            content: "已经有9张图片了,是否清空现有图片？",
			            success: (e) => {
			                if (e.confirm) {
			                    this.imageList = [];
			                    res(true);
			                } else {
			                    res(false)
			                }
			            },
			            fail: () => {
			                res(false)
			            }
			        })
			    })
			},
            previewImage: function(e) {
				console.log(e.target.dataset.src)
                var current = e.target.dataset.src
                uni.previewImage({
                    current: current,
                    urls: this.imageList
                })
            },
			chooseImage: async function() {









			
			    if (this.imageList.length === 9) {
			        let isContinue = await this.isFullImg();
			        console.log("是否继续?", isContinue);
			        if (!isContinue) {
			            return;
			        }
			    }
				let that = this;
			    uni.chooseImage({
			        sourceType: sourceType[this.sourceTypeIndex],
			        sizeType: sizeType[this.sizeTypeIndex],
			        count: this.imageList.length + this.count[this.countIndex] > 9 ? 9 - this.imageList.length :
			            this.count[this.countIndex],
			        success: (res) => {
						
							console.log(res)
						const tempFilePaths = res.tempFilePaths;
						uni.uploadFile({
							url: that.server + '/api/common/uploadImage.json', //仅为示例，非真实的接口地址
							filePath: tempFilePaths[0],
							name: 'file',
							success: (uploadFileRes) => {
								console.log(uploadFileRes)
								//上传成功
								let response = JSON.parse(uploadFileRes.data);
								if (response.status === "2000000") {
									// response.data.url
									//加上那些没有http开头链接的
									that.diseaseImgList = that.diseaseImgList.concat(response.data.url);
									that.imageList = that.imageList.concat(res.tempFilePaths);
								}
							}
						});
			        },
			        fail: (err) => {





			        }
			    })
			},
			getDate(type) {
				const date = new Date();
				let year = date.getFullYear();
				let month = date.getMonth() + 1;
				let day = date.getDate();
				if (type === 'start') {
					year = year - 60;
				} else if (type === 'end') {
					year = year + 2;
				}
				month = month > 9 ? month : '0' + month;;
				day = day > 9 ? day : '0' + day;
				return `${year}-${month}-${day}`;
			},
			getTypeList(){
				let that = this;
				let params = {};
				tools.request("/api/my/typeIllnessList.json", params).then(function(data) {
					that.typeList = data.typeIllnessDTOList;
					
					that.typeList.map( item => {
						that.typeNameList.push(item.name);
					})
					
					if(that.typeList.length > 0){
						that.typeId = that.typeList[0].id
					}
				})
			},
			bindTypeChange(event){
				this.index = event.detail.value
				this.typeId = this.typeList[this.index].id;
			},
			bindTimeChange(event){
				this.date = event.detail.value;
			}
		}
	}
